import Titling from "../home/titling"
import info from './info.js'
import TeamCards from "../home/teamCard"

const Team = () => (
    <main>
        
        <Titling
            title={info.title}
            subtitle={info.subtitle}
        />
        
        <TeamCards people={info.owners} isOwners className="mt-4 max-w-6xl mx-auto" />
        <TeamCards people={info.employees} className="mt-8 max-w-6xl mx-auto" />
        
    </main>
)

export default Team

